<template>
	<view>
		<view class="item-row" v-for="(item, index) in items" :key="index" >
			<view class="avatar"><image :src="item.cover" class="avatar" /></view>
			<view class="content">
				<view class="share-title">{{ item.title }}</view>
				<view class="content-title">{{ item.summary.substring(0, 25) }}……</view>
			</view>
			<view class="score">
				<view class="content-title">下载</view>
			</view>
		</view>
	</view>
</template>

<script>

export default {
	data() {
		return {
			items: []
		};
	},
	onLoad() {
		this.items = uni.getStorageSync("items")
	},
	methods: {
		getShare: function() {
			console.log(">>>>>>>>>>>>>>>>>>")
			uni.request({
				url: 'http://120.26.184.121:8040/shares/query',
				method: 'GET',
				header: {
					'content-type': 'application/json',
				},
				success: res => {
					console.log('进入回调函数');
					this.items = res.data.data;
					console.log()
				}
			});
		},

	}
};
</script>

<style>
.item-row {
	height: 160upx;
	padding: 20upx;
	display: flex;
	justify-content: space-around;
	border-bottom: 1px solid #dfdfdf;
}
.avatar {
	width: 120upx;
	height: 120upx;
	border-radius: 50%;
}
.content {
	width: 60%;
	display: flex;
	flex-direction: column;
}
.score {
	width: 15%;
	display: flex;
	flex-direction: column;
}
.share-title {
	font-weight: 600;
	font-size: 14px;
}
.content-title {
	font-size: 13px;
}
</style>
